.popup {
  display: flex;
  width: 100%;
  min-height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  align-content: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, .5);
  visibility: hidden;
  opacity: 0;
  z-index: 10;
  /*transition: visibility .3s, opacity .3s linear;*/
}
